<template>
  <div class="hello">
    <el-button type="primary" @click="login">主要按钮</el-button>
    <h1 >{{ msg }}</h1>
    <h2>Essential Links</h2>
    
  </div>
</template>

<script>
import {requestLogin} from '../api/user'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    login () {
      // alert(1)
      requestLogin({'name':'linjinze999'}).then(data => this.$alert(data))
      // request('/api/user/login', {'name':'linjinze999'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



